"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import Header from "@/components/header"
import { Button } from "@/components/ui/button"
import { Trash2, Plus, Minus, ShoppingBag } from "lucide-react"
import { useAuth } from "@/lib/auth-context"

interface CartItem {
    id: string
    name: string
    price: number
    quantity: number
    image: string
    kitchenName: string
}

export default function CartPage() {
    const router = useRouter()
    const { isAuthenticated, user } = useAuth()
    const [cartItems, setCartItems] = useState<CartItem[]>([])
    const [loading, setLoading] = useState(true)

    useEffect(() => {
        if (!isAuthenticated || user?.role !== 'customer') {
            router.push('/auth/login')
            return
        }

        // Load cart from localStorage
        const savedCart = localStorage.getItem('cloudbites_cart')
        if (savedCart) {
            try {
                setCartItems(JSON.parse(savedCart))
            } catch (error) {
                console.error('Error loading cart:', error)
            }
        }
        setLoading(false)
    }, [isAuthenticated, user, router])

    const saveCart = (items: CartItem[]) => {
        localStorage.setItem('cloudbites_cart', JSON.stringify(items))
        setCartItems(items)
    }

    const updateQuantity = (itemId: string, delta: number) => {
        const updatedCart = cartItems.map(item => {
            if (item.id === itemId) {
                const newQuantity = Math.max(1, item.quantity + delta)
                return { ...item, quantity: newQuantity }
            }
            return item
        })
        saveCart(updatedCart)
    }

    const removeItem = (itemId: string) => {
        const updatedCart = cartItems.filter(item => item.id !== itemId)
        saveCart(updatedCart)
    }

    const clearCart = () => {
        saveCart([])
    }

    const subtotal = cartItems.reduce((sum, item) => sum + item.price * item.quantity, 0)
    const deliveryFee = subtotal > 0 ? 40 : 0
    const tax = subtotal * 0.05 // 5% tax
    const total = subtotal + deliveryFee + tax

    const handleCheckout = () => {
        router.push('/checkout')
    }

    if (loading) {
        return (
            <div className="min-h-screen bg-background">
                <Header cartCount={cartItems.length} />
                <div className="max-w-6xl mx-auto px-4 py-12">
                    <div className="animate-pulse">
                        <div className="h-8 bg-neutral-200 rounded w-1/4 mb-8"></div>
                        <div className="space-y-4">
                            {[...Array(3)].map((_, i) => (
                                <div key={i} className="h-32 bg-neutral-200 rounded"></div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    return (
        <div className="min-h-screen bg-background">
            <Header cartCount={cartItems.length} />

            <div className="max-w-6xl mx-auto px-4 py-12">
                <h1 className="text-3xl font-bold mb-8">Shopping Cart</h1>

                {cartItems.length === 0 ? (
                    <div className="text-center py-16">
                        <ShoppingBag className="w-24 h-24 mx-auto text-neutral-300 mb-4" />
                        <h2 className="text-2xl font-semibold mb-2">Your cart is empty</h2>
                        <p className="text-muted-foreground mb-6">Add some delicious food to get started!</p>
                        <Button onClick={() => router.push('/')}>Browse Cloud Kitchens</Button>
                    </div>
                ) : (
                    <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                        {/* Cart Items */}
                        <div className="lg:col-span-2 space-y-4">
                            {cartItems.map((item) => (
                                <div
                                    key={item.id}
                                    className="bg-white rounded-lg shadow-sm p-4 flex gap-4 items-center border border-neutral-200"
                                >
                                    <img
                                        src={item.image}
                                        alt={item.name}
                                        className="w-24 h-24 object-cover rounded-lg"
                                    />
                                    <div className="flex-1">
                                        <h3 className="font-semibold text-lg">{item.name}</h3>
                                        <p className="text-sm text-muted-foreground">{item.kitchenName}</p>
                                        <p className="text-primary font-semibold mt-2">₹{item.price}</p>
                                    </div>
                                    <div className="flex items-center gap-3">
                                        <button
                                            onClick={() => updateQuantity(item.id, -1)}
                                            className="w-8 h-8 flex items-center justify-center rounded-full border border-neutral-300 hover:bg-neutral-100"
                                            disabled={item.quantity <= 1}
                                        >
                                            <Minus className="w-4 h-4" />
                                        </button>
                                        <span className="font-semibold w-8 text-center">{item.quantity}</span>
                                        <button
                                            onClick={() => updateQuantity(item.id, 1)}
                                            className="w-8 h-8 flex items-center justify-center rounded-full border border-neutral-300 hover:bg-neutral-100"
                                        >
                                            <Plus className="w-4 h-4" />
                                        </button>
                                    </div>
                                    <button
                                        onClick={() => removeItem(item.id)}
                                        className="text-red-500 hover:text-red-600 p-2"
                                    >
                                        <Trash2 className="w-5 h-5" />
                                    </button>
                                </div>
                            ))}

                            <Button
                                variant="outline"
                                onClick={clearCart}
                                className="w-full"
                            >
                                Clear Cart
                            </Button>
                        </div>

                        {/* Order Summary */}
                        <div className="lg:col-span-1">
                            <div className="bg-white rounded-lg shadow-sm p-6 border border-neutral-200 sticky top-24">
                                <h2 className="text-xl font-semibold mb-4">Order Summary</h2>
                                <div className="space-y-3 mb-4">
                                    <div className="flex justify-between">
                                        <span className="text-muted-foreground">Subtotal</span>
                                        <span className="font-semibold">₹{subtotal.toFixed(2)}</span>
                                    </div>
                                    <div className="flex justify-between">
                                        <span className="text-muted-foreground">Delivery Fee</span>
                                        <span className="font-semibold">₹{deliveryFee.toFixed(2)}</span>
                                    </div>
                                    <div className="flex justify-between">
                                        <span className="text-muted-foreground">Tax (5%)</span>
                                        <span className="font-semibold">₹{tax.toFixed(2)}</span>
                                    </div>
                                    <div className="border-t border-neutral-200 pt-3 flex justify-between text-lg">
                                        <span className="font-bold">Total</span>
                                        <span className="font-bold text-primary">₹{total.toFixed(2)}</span>
                                    </div>
                                </div>
                                <Button onClick={handleCheckout} className="w-full" size="lg">
                                    Proceed to Checkout
                                </Button>
                                <Button
                                    variant="outline"
                                    onClick={() => router.push('/')}
                                    className="w-full mt-3"
                                >
                                    Continue Shopping
                                </Button>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    )
}
